@import url('https://fonts.googleapis.com/css?family=Roboto');

html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}
body {
    background: linear-gradient(10deg,#000000,#ffffff, #ffffff);
    text-align: center;
    font-family: 'Roboto', sans-serif;
}
#drum_machine {
    text-align: center;
}

.display-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: linear-gradient(1800deg,rgb(0, 0, 0),rgb(255, 255, 255),rgb(255, 255, 255));
    height: 100vh;
}
/*the container for the pad buttons */
#display-pads {
    display: grid;
    grid-template-columns: repeat(3,minmax(20px,100px));
    grid-template-rows: 60px 60px 60px;
    grid-gap: 5px;
    margin-top: 50px;
    
}
/*style the buttons */
.drum-pad {
    padding: 20px;
    cursor: pointer;
    background-color: #333333;
    color : #fff ;
    /* box-shadow: 0 0 0 1px rgba(0,0,0,.1), 0 2px 3px rgba(0,0,0,.2); */
    border-radius: 4px;
    transition: all 400ms ease-in-out;
    font-size: 30px;
    font-weight: bold;
    font-family: 'Roboto', sans-serif;
    text-shadow: 0 3px 4px 1px rgba(0,0,0,.1), 0 2px 3px rgba(0,0,0,.2);
    text-align: center;
}
.drum-pad:hover{
    background-color: #404040;
}
.drum-pad:active{
    transform: scale(1.05);
}
.drum-pad:hover{
  background-color:#9f9e9e;
}

.current-text {
    text-align: center;
    position: relative;
    font-size: 50px;
    font-family: 'Roboto', sans-serif;
    color:#111111;

}


.volume {
  margin: auto;
  text-align: center;
}



